export default {
  query(data) {
    return $ec.http.send('/dts/plm/q/pcTBmUnit/pageQueryCancelUnitDataPage/v1', data);
  },
  cancellation(data) {
    return $ec.http.send('/dts/plm/c/pcTBmUnit/cancelPcTBmUnit/v1', data);
  },
  cancellationedQuery(data) {
    return $ec.http.send('/dts/plm/q/pcTBmUnit/pageQueryCanceledUnitData/v1', data);
  },
};
