export default {
  query(data) {
    return $ec.http.send('/dts/plm/q/pcTDtAuditProcess/pageQueryPcTDtAuditProcess/v1', data);
  },
  queryAlone(data) {
    return $ec.http.send('/dts/plm/q/pcTDtAuditProcess/getPcTDtAuditProcessById/v1', data);
  },
  Add(data) {
    return $ec.http.send('/dts/plm/c/pcTDtAuditProcess/createPcTDtAuditProcess/v1', data);
  },
  UpdateStatus(data) {
    return $ec.http.send(
      '/dts/plm/c/pcTDtAuditProcess/updatePcTDtAuditProcessForEnableFlag/v1',
      data,
    );
  },
};
