export default {
  query(data) {
    return $ec.http.send('/dts/plm/q/pcTBmUnit/pageQueryClearPcTBmUnit/v1', data);
  },
  clear(data) {
    return $ec.http.send('/dts/plm/c/pcTBmUnit/clearPcTBmUnit/v1', data);
  },
  clearQuery(data) {
    return $ec.http.send('/dts/plm/q/pcTBmUnit/pageQueryClearedUnitData/v1', data);
  },
  calculate(data) {
    return $ec.http.send('/dts/plm/c/pcTBmUnit/countAllocationProportion/v1', data);
  },
};
