export default {
  // 初始化数据
  query(data) {
    return $ec.http.send('/dts/plm/bpm/q/pcTLossReportQty/pagePcTLossReportQty/v1', data);
  },
  // 新增
  add(data) {
    return $ec.http.send('/dts/plm/bpm/c/pcTLossReportQty/createPcTLossReportQty/v1', data);
  },
  // 编辑
  onEdit(data) {
    return $ec.http.send('/dts/plm/bpm/q/pcTLossReportQty/getPcTLossReportQtyById/v1', data);
  },
  // 修改
  onChange(data) {
    return $ec.http.send('/dts/plm/bpm/c/pcTLossReportQty/updatePcTLossReportQty/v1', data);
  },
  // 删除
  onDelete(data) {
    return $ec.http.send('/dts/plm/bpm/c/pcTLossReportQty/deletePcTLossReportQtyByIds/v1', data);
  },
  // 上报
  onReported(data){
    return $ec.http.send('/dts/plm/bpm/c/pcTLossReportQty/updateBatchReportedById/v1', data);
  },
  // 同意
  onAgree(data){
    return $ec.http.send('/dts/plm/bpm/c/pcTLossReportQty/updateBatchAgreeById/v1', data);
  },
  // 退回
  onBack(data){
    return $ec.http.send('/dts/plm/bpm/c/pcTLossReportQty/updateBatchReturnById/v1', data);
  }
}