export default {
  query(data) {
    return $ec.http.send('/dts/plm/q/pcTNeedCollectMas/queryPcTNeedCollectMas/v1', data);
  },

  save(data) {
    return $ec.http.send('/dts/plm/c/pcTNeedCollectBatch/pcTNeedCollectMasCreate/v1', data);
  },

  delete(data) {
    return $ec.http.send('/dts/plm/q/pcTNeedCollectMas/pcTNeedCollectMasDelete/v1', data);
  },

  upload(data) {
    return $ec.http.send('/dts/plm/q/pcTNeedCollectMas/pcTNeedCollectMasReport/v1', data);
  },

  queryLevel(data) {
    return $ec.http.send('/dts/bim/q/cmTLeafLevel/pageQueryCmTLeafLevels/v1', data);
  },

  queryAllLevel(data) {
    return $ec.http.send('/dts/bim/c/cmTLeafLevel/spinnerLeafLevelEx/v1', data);
  },
};
