export default {
  query(data) {
    return $ec.http.send(
      '/dts/plm/c/pcTAsIndustryTobaccoProv/findPcAsIndustryTobaccoProv/v1',
      data,
    );
  },

  queryTobaccoProvOrgCode(data) {
    return $ec.http.send(
      '/dts/plm/c/pcTAsIndustryTobaccoProv/findPcAsIndustryTobaccoProvByIndustry/v1',
      data,
    );
  },

  save(data) {
    return $ec.http.send(
      '/dts/plm/c/pcTAsIndustryTobaccoProv/savePcAsIndustryTobaccoProv/v1',
      data,
    );
  },
};
