import Vue from 'vue';
import VueRouter from 'vue-router';
import NProgress from 'nprogress';
import 'nprogress/nprogress.css';
import { hasLS, getLS } from '@euler-material/ecm-utils';

Vue.use(VueRouter);

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [],
});

router.beforeEach(async (to, from, next) => {
  if (to.path !== from.path) {
    NProgress.start();
  }

  if (process.env.NODE_ENV === 'production') {
    // 生产环境：当本地appConfig过期，或者切换用户角色，比对userId是否一致。
    const session = await $ec.session.get();
    if (!hasLS('APP_CONFIG') || session.userId !== getLS('APP_CONFIG').userId) {
      router.app.$options.store.dispatch('fetchConfigInit');
    }
  } else {
    // 开发环境：可以直接去掉判断条件，每次页面重置本地缓存
    router.app.$options.store.dispatch('fetchConfigInit');
  }

  next();
});

router.afterEach(() => {
  NProgress.done();
});

export default router;
