export default {
  query(data) {
    return $ec.http.send(
      '/dts/plm/q/pcTAsFocusDealMas/pageQueryPcTAsFocusDealMasDataForTb/v1',
      data,
    );
  },
  queryRowlist(data) {
    return $ec.http.send('/dts/plm/q/pcTAsFocusDealMas/getPcAsFocusDealDetailDataForTb/v1', data);
  },
  queryRowBaseDetails(data) {
    return $ec.http.send(
      '/dts/plm/q/pcTAsFocusDealDetl/getPcAsFocusDealBaseDetailDataForTb/v1',
      data,
    );
  },
  getDetails(data) {
    return $ec.http.send(
      '/dts/plm/q/pcTAsFocusDealMas/getPcAsFocusDealAllDetailDataForTb/v1',
      data,
    );
  },
  // 编辑按钮
  updateButton(data) {
    return $ec.http.send('/dts/plm/s/pcTAsFocusDealMas/savePcAsFocusDealMasGjjData/v1', data);
  },
  getPass(data) {
    return $ec.http.send('/dts/plm/u/pcTAsFocusDealMas/auditPcAsFocusDealDataGjj/v1', data);
  },
};
