      //   component: () => import('@/views/bpm/execution-audit/Index.vue'),
      // },
      // {
      //   path: '/plm/bpm/adjust-audit',
      //   name: 'buyPlanAdjustAudit',
      //   meta: {
      //     icon: 'onetomany',
      //     title: '执行量调整登记审核',
      //     colorCode: '001',
      //     name: 'buyPlanAdjustAudit',
      //     keepAlive: true,
      //     permission: 'buy-plan-adjust-audit',
      //   },
      //   component: () => import('@/views/bpm/execution-audit-adjust/Index.vue'),
      // },
