export default {
  query(data) {
    return $ec.http.send('/dts/plm/q/pcTAsApprTpl/queryPcTAsApprTplFocus/v1', data);
  },
  update(data) {
    return $ec.http.send('/dts/plm/u/pcTAsApprTpl/updateApprTplEntryDate/v1', data);
  },
  queryDetails(data) {
    return $ec.http.send('/dts/plm/q/pcTAsApprTpl/getDetailMessageForFocus/v1', data);
  },
  getParamsByCode(data) {
    return $ec.http.send('/dts/plm/q/pcTAsApprTpl/getParamInfoByCode/v1', data);
  },
};
