export default {
  before() {
    return $ec.http.send('/dts/plm/q/pcTAsApprTpl/getParamInfoByCode/v1');
  },
  query(data) {
    return $ec.http.send('/dts/plm/q/pcTAsApprTpl/queryPcTAsApprTplAdjustPage/v1', data);
  },
  get(data) {
    return $ec.http.send('/dts/plm/q/pcTAsApprTpl/getDetailMessageForBaseCell/v1', data);
  },
  add(data) {
    return $ec.http.send('/dts/plm/c/pcTAsApprTpl/createPcTAsApprTplAdjust/v1', data);
  },
  confirm(data) {
    return $ec.http.send('/dts/plm/u/pcTAsApprTpl/updatePcTAsApprTplAuditBatch/v1', data);
  },
  queryDaily(data) {
    return $ec.http.send('/dts/plm/q/pcTAsApprTpl/getDailyAppTplForAdd/v1', data);
  },
  queryOrgTree(data) {
    return $ec.http.send('/dts/bim/q/CmTOrgMainServiceExAPI/findCmTOrgAllChild/v1', data);
  },
  delete(data) {
    return $ec.http.send('/dts/plm/d/pcTAsApprTpl/deletePcTAsApprTpl/v1', data);
  },
  queryJdy(data) {
    return $ec.http.send('/dts/plm/q/pcTAsApprTpl/getDetailMessageForBaseCell/v1', data);
  },
  editQuery(data) {
    return $ec.http.send('/dts/plm/q/pcTAsApprTpl/getDetailMessageForDetail/v1', data);
  },
};
