export default {
  query(data) {
    return $ec.http.send('/dts/plm/q/pcTNeedCollectBatch/pageQueryPcTNeedCollectBatch/v1', data);
  },

  update(data) {
    return $ec.http.send(
      '/dts/plm/c/pcTNeedCollectBatch/canSavePcTNeedCollectBatchRequest/v1',
      data,
    );
  },
  save(data) {
    return $ec.http.send('/dts/plm/c/pcTNeedCollectBatch/createPcTNeedCollectBatch/v1', data);
  },

  delete(data) {
    return $ec.http.send('/dts/plm/c/pcTNeedCollectBatch/deletePcTNeedCollectBatch/v1', data);
  },

  finish(data) {
    return $ec.http.send('/dts/plm/c/pcTNeedCollectBatch/completePcTNeedCollectBatch/v1', data);
  },
};
