export default {
  confirmOK(data) {
    return $ec.http.send('/dts/plm/u/pcTAsFocusDealMas/auditPcAsFocusDealData/v1', data);
  },
  Reject(data) {
    return $ec.http.send('/dts/plm/u/pcTAsFocusDealMas/rejectPcAsFocusDealData/v1', data);
  },
  getDetails(data) {
    return $ec.http.send(
      '/dts/plm/q/pcTAsFocusDealMas/getPcAsFocusDealAllDetailDataForTb/v1',
      data,
    );
  },
  onGetdetails(data) {
    return $ec.http.send(
      '/dts/plm/q/pcTAsFocusDealDetl/getPcAsFocusDealBaseDetailDataForTb/v1',
      data,
    );
  },
};
