export default {
  query(data) {
    return $ec.http.send('/dts/plm/q/pcTBmUnitPlan/queryPcTBmUnitPlan/v1', data);
  },
  getBuYear(data) {
    return $ec.http.send('/dts/bim/q/cmTYear/getYearByOrgAndType/v1', data);
  },
  getOrgCode(data) {
    return $ec.http.send('/dts/plm/q/userInfo/getUserInfo/v1', data);
  },
  save(data) {
    return $ec.http.send('/dts/plm/c/pcTBmUnitPlan/saveOrUpdateBaseUnitInfo/v1', data);
  },
  send(data) {
    return $ec.http.send('/dts/plm/c/pcTBmUnitPlan/issuedPcTBmUnitPlan/v1', data);
  },
};
