export default {
  // 所有的枚举在这里集中处理

  queryContent(data) {
    return $ec.http.send('/dts/bim/q/cmTEnumType/listCmTEnumTypeByCodes/v1', data);
  },

  queryLeafForm(data) {
    return $ec.http.send('/dts/bim/q/cmTLeafForm/spinnerLeafFormEx/v1', data);
  },
  queryStore(data) {
    return $ec.http.send('/dts/bim/q/cmTStorage/listCmTStorageByOrgCode/v1', data);
  },

  queryLeafType(data) {
    return $ec.http.send('/dts/bim/c/cmTLeafType/spinnerLeafTypeEx/v1', data);
  },

  queryTreeByCode(data) {
    return $ec.http.send('/dts/bim/q/CmTOrgMainServiceExAPI/findAllChildOrgList/v1', data);
  },

  queryContType(data) {
    return $ec.http.send('/dts/bsa/q/lcTContTypeEx/getLcTContTypeExByUseType/v1', data);
  },
};
