export default {
  query(data) {
    return $ec.http.send('/dts/plm/c/pcTBuPlan/queryPcTBuPlan/v1', data);
  },

  queryEditData(data) {
    return $ec.http.send('/dts/plm/c/pcTBuPlan/queryEditPcTBuPlan/v1', data);
  },

  save(data) {
    return $ec.http.send('/dts/plm/c/pcTBuPlan/savePcTBuPlanList/v1', data);
  },

  queryDetail(data) {
    return $ec.http.send('/dts/plm/q/pcTBuPlanOprRecord/queryPcTBuPlanOprRecordDetailed/v1', data);
  },

  queryTotalData(data) {
    return $ec.http.send('/dts/plm/c/pcTBuPlan/sumPcTBuPlan/v1', data);
  },

  agree(data) {
    return $ec.http.send('/dts/plm/c/pcTBuPlan/agreePcTBuPlan/v1', data);
  },
};
