export default {
  query(data) {
    return $ec.http.send(
      '/dts/plm/q/pcTNeedCollectPlanAmount/queryPcTNeedCollectPlanAmountManage/v1',
      data,
    );
  },

  save(data) {
    return $ec.http.send(
      '/dts/plm/u/pcTNeedCollectPlanAmount/pcTNeedCollectPlanAmountSave/v1',
      data,
    );
  },

  queryBatch(data) {
    return $ec.http.send(
      '/dts/plm/q/pcTNeedCollectPlanAmount/selectPcTNeedCollectPlanAmount/v1',
      data,
    );
  },

  queryNeedGatherYear(data) {
    return $ec.http.send(
      '/dts/plm/q/pcTNeedCollectBatch/pcTNeedCollectBatchSelectNeedYear/v1',
      data,
    );
  },
};
