export default {
  QueryList(data) {
    return $ec.http.send('/dts/plm/q/pcTAsDailyDealMas/queryPagePcTAsDailyDealMas/v1', data);
  },
  QueryInit(data) {
    return $ec.http.send('/dts/plm/q/pcTAsDailyDealMas/pcTAsDailyDealMasAuditFormat/v1', data);
  },
  updateStatus(data) {
    return $ec.http.send('/dts/plm/c/pcTAsDailyDealMas/updateAndAuditAgreeDailyMas/v1', data);
  },
  updateStatusAll(data) {
    return $ec.http.send('/dts/plm/c/pcTAsDailyDealMas/updateBatchSendDailyMas/v1', data);
  },
  updateStatusExit(data) {
    return $ec.http.send('/dts/plm/c/pcTAsDailyDealMas/updateAndRejectDailyMas/v1', data);
  },
  Aband(data) {
    return $ec.http.send('/dts/plm/c/pcTAsDailyDealMas/updateAndAbandonDailyMas/v1', data);
  },
  LastAudit(data) {
    return $ec.http.send('/dts/plm/c/pcTAsDailyDealMas/updateAndLastAuditDailyMas/v1', data);
  },
  UltiMateAudit(data) {
    return $ec.http.send('/dts/plm/q/pcTAsDailyDealMas/queryPagePcTAsDailyDealMas/v1', data);
  },
  download(data) {
    return $ec.http.send('/dts/plm/c/file/downloadPcTAsAttachFile/v1', data);
  },
};
