import { message } from 'ant-design-vue';
// 'development',
// const isPerm = ['development', 'production'].includes(process.env.NODE_ENV);
const isPerm = ['production'].includes(process.env.NODE_ENV);
const permission = isPerm
  ? {
      mode: 'min-euler',
      // session 请求路径，任意微服务上的
      sessionUrl: '/dts/plm/session/get',
      cookieUrl: '/dts/plm/session/checkCookie', // 验证 cookie 是否过期
      // 查询应用树，目前只用于小欧拉，默认不用配置
      // appUrl: "/euler/mp/q/resource/listAccessibleAppTree",
    }
  : false;
export default {
  appKey: '000001',
  secret: 'secret',
  version: '1.0',
  // 全局 http 请求遮罩
  Loading: false,
  // 获取系统配置，生产环境下需开启（非平台后端架构则不需要）
  SystemConfig: false,
  http: {
    baseURL: '',
    headers: {
      // 请求头
    },
  },
  // 全局 http 错误回调
  error(msg) {
    message.error(msg);
  },
  // apis 路径
  apis: require.context('@/apis', true, /\.js$/),
  // 权限控制
  permission,
  // 全局控件配置
  components: {
    EcPager: {
      defaultPageSize: 10,
      pageSizeOptions: ['10', '15', '30', '50', '100'],
    },
    EcContainer: {
      showTitle: true,
      height: 'auto',
    },
  },
};
