export default {
  query(data) {
    return $ec.http.send('/dts/plm/q/pcTBmUnitLinkRec/pageQueryUpdatePcTBmUnitLinkRec/v1', data);
  },
  delete(data) {
    return $ec.http.send('/dts/plm/c/pcTBmUnitLinkRec/deleteBaseUnit/v1', data);
  },
  add(data) {
    return $ec.http.send('/dts/plm/c/pcTBmUnitLinkRec/saveBaseUnit/v1', data);
  },
  update(data) {
    return $ec.http.send('/dts/plm/c/pcTBmUnitLinkRec/updateBaseUnit/v1', data);
  },
  auxiliaryInfo(data) {
    return $ec.http.send('/dts/plm/q/pcTBmUnitLinkRec/getLinkedBaseUnitInfo/v1', data);
  },
};
