/**
 * @date: 2022-01-07 15:42:37
 * @author: wangzt
 * @description:
 * hideInMenu: true 则不会显示在菜单栏
 * hideChildren: true 则其子路由不显示在菜单栏
 * layout: false 路由不会配置到layout的main中,即整个窗口显示
 * component: EcView 中间层路由使用 EcView 替代 {render: (h) => h("router-view")}
 * 可通过设置其子路由的meta内keepAlive属性为false控制其是否缓存,默认为缓存
 * meta.title 菜单标题 不设置则不会显示在菜单栏中
 * meta.icon 菜单图标
 * meta.colorCode 图标背景色号 '001'~'012'
 * meta.name 组件的name(缓存使用)
 * meta.keepAlive 是否缓存(若需缓存,name必须且keepAlive为true)
 * meta.hideInNavTabs: true 则不会显示在导航标签栏
 */
import { EcView } from '@euler/pc-ui';
// 调拨计划
import apm from './apm';
// 收购计划
import buyPlanManage from './buyPlanManage';
import packingSpecManage from './packingSpecManage';
import pcm from './pcm';

export default [
  {
    path: '/',
    redirect: '/home',
  },
  {
    path: '/home',
    meta: {
      title: '主页',
      icon: 'home',
      name: 'Home',
      keepAlive: true,
      hideInNavTabs: true,
    },
    name: 'home',
    component: () => import(/* webpackChunkName: 'other' */ '@/views/Home'),
  },
  {
    path: '/plm',
    name: 'plm',
    component: EcView,
    meta: {
      title: '计划管控(开发)',
      name: 'plm',
      keepAlive: true,
      hideInNavTabs: true,
      permission: 'plm',
    },
    children: [...apm, ...buyPlanManage, ...packingSpecManage, ...pcm],
  },
];
