export default {
  query(data) {
    return $ec.http.send('/dts/plm/q/pcTBmUnitLinkRec/pageQueryPcTBmUnitLinkRec/v1', data);
  },
  add(data) {
    return $ec.http.send('/dts/plm/c/pcTBmUnitLinkRec/updateNewBaseUnitLinkLog/v1', data);
  },
  get() {
    return $ec.http.send('/dts/plm/q/pcTBmUnitLinkRec/getLinkedBaseUnitInfo/v1');
  },
  addQuery(data) {
    return $ec.http.send('/dts/plm/q/pcTBmUnitLinkRec/pageQueryAddPcTBmUnitLinkRec/v1', data);
  },
  existingQuery(data) {
    return $ec.http.send('/dts/plm/q/pcTBmUnitLinkRec/pageQueryUsedPcTBmUnit/v1', data);
  },
  addSave(data) {
    return $ec.http.send('/dts/plm/c/pcTBmUnitLinkRec/updateNewBaseUnitLinkLog/v1', data);
  },
  existingSave(data) {
    return $ec.http.send('/dts/plm/c/pcTBmUnitLinkRec/addOrUpdateOldBaseLinkLog/v1', data);
  },
  brandQuery(data) {
    return $ec.http.send('/dts/plm/q/pcTBmUnitLinkRec/getBrandList/v1', data);
  },
  delete(data) {
    return $ec.http.send('/dts/plm/c/pcTBmUnitLinkRec/deletePcTBmUnitLinkRec/v1', data);
  },
};
