export default {
  query(data) {
    return $ec.http.send('/dts/plm/pac/q/pcTCpManage/pageQueryPcTCpManage/v1', data);
  },

  queryTotalData(data) {
    return $ec.http.send('/dts/plm/pac/q/pcTCpManage/queryHead/v1', data);
  },

  queryDetail(data) {
    return $ec.http.send('/dts/plm/pac/q/pcTCpManage/queryDetail/v1', data);
  },

  queryProcess(data) {
    return $ec.http.send('/dts/plm/pac/q/pcTCpManage/pageConfCp/v1', data);
  },
};
