export default {
  query(data) {
    return $ec.http.send('/dts/plm/q/pcTBuPlanOprRecord/queryPcTBuPlanOprRecordIssue/v1', data);
  },

  issued(data) {
    return $ec.http.send('/dts/plm/q/pcTBuPlanOprRecord/releasePcTBuPlanOprRecordAdjust/v1', data);
  },

  queryAdjust(data) {
    return $ec.http.send(
      '/dts/plm/q/pcTBuPlanOprRecord/pageQueryPcTBuPlanOprRecordAdjustIssue/v1',
      data,
    );
  },

  issuedAdjust(data) {
    return $ec.http.send('/dts/plm/q/pcTBuPlanOprRecord/releasePcTBuPlanOprRecordAdjust/v1', data);
  },
  beforeAdjust(data) {
    return $ec.http.send(
      '/dts/plm/q/pcTBuPlanOprRecord/getAdjustBeforePcTBuPlanOprRecord/v1',
      data,
    );
  },
  Export(data) {
    return $ec.http.send('/dts/plm/c/pcTBuPlanOprRecord/exportIssueDataToExcel/v1', data);
  },
};
